<template>
  <div class="heder-nav">
    <div class="header-left">
      <div class="logo"><img src="../../assets/img/logo.svg" alt=""></div>
      <div class="nav">
        <div class="nav-menu">
          <router-link :to="{path:i.tag}" active-class="active" v-for="(i,index) in navList" @click.native="toMenu(index)" :key="index">{{i.navname}}</router-link>
        </div>
      </div>
    </div>
    <div class="right-line">
      <div class="cur-balance">
        <em>{{netName}}</em>
        <span>{{balanceOkt}} OKT</span>
      </div>
      <div class="header-right" v-if="curAddr !== ''">
        <div class="conect-net" @click="addrInfor">{{curAddr}}</div>
      </div>
      <div class="header-right" v-else>
        <div class="conect-net2" @click="connectWallet">Connect to a wallet</div>
      </div>
      
    </div>
    
    <!--  -->
    <van-popup v-model="showLine" class="account-infor" closeable>
      <h3>Account</h3>
      <div class="account-infor-line">
        <h4>Connected with MetaMask</h4>
        <h2>{{curAddr}}</h2>
        <div class="view-infor">
          <div class="copy-addr tag-read" :data-clipboard-text="accountAddr" @click="copyAddr()">Copy Address</div>
          <div class="link-detail">
            <el-link :href="skipLink" target="_blank">View on Etherccan</el-link>
          </div>
        </div>
      </div>
      <h5>Your transactions will appear here...</h5>
    </van-popup>

    <van-popup v-model="switchWallet"  class="wallets">
      <div class="switch">
        <div class="top">

          <h1>Connect to a wallet</h1>      
          <span>
            <i class="el-icon-close " @click="switchWallet=false"></i>
          </span>    
        </div>
        <div class="line"></div>
        <div class="lists">
          <div class="list" v-for="(item,index) in wallets" :key="index" @click="selectWallet">
            <span>{{item.txt}}</span>
            <img :src="item.icon" alt="">
          </div>
          
        </div>
        <div class="arrow" @click="showMore.status=!showMore.status">
          <span>
            {{showMore.txt}}
          </span>
            <i class="el-icon-arrow-down" :class="{ 'arrowTransform': !showMore.status, 'arrowTransformReturn': showMore.status}"></i>
        </div>
        <div class="bottom">
          <i class="el-icon-question"></i>
          <a href="javascript:;">Learn how to connect</a>
        </div>
      </div>
      
      
    </van-popup>

  </div>
</template>
<script>
import NCWeb3 from "../../assets/js/web3"
import { Toast } from 'vant'
import Clipboard from 'clipboard'
import { Popup } from 'vant'
export default {
  name: '',
  components: {
    [Popup.name]: Popup,
  },
  props: {},
  watch:{
    'showMore.status'(curr,old){
      if(curr){
      this.showMore.txt="Show More";
      this.wallets = this.walletsShort;

      }
      else{
      this.showMore.txt="pack up";
      this.wallets = this.walletsLong;

      }

    }
  },
  data () {
    return {
      showMore:{
        status:true,
        txt:"Show More"
      },
      switchWallet:false,
      navList: [
        {
          navname:"Exchange",
          tag:"/Home"
        },
        { 
          navname: 'Mix',
          tag:"/Mix"
        },
        { 
          navname: 'Flow Mining',
          tag:"/Flowmining"
        },
        { 
          navname: 'Market',
          tag:"/Market"
        },
      ],
      curAddr:'',
      showLine: false,
      accountAddr: '',
      walletAddress: null,
      skipLink:'',
      balanceOkt:'',
      wallets:[
        {
          icon:require("../../assets/img/wallet/okt.png"),
          txt:"OKExWallet"
        },
        {
          icon:require("../../assets/img/wallet/metamask-fox.svg"),
          txt:"Metamask"
        },
        
      ],
      walletsLong:[
        {
          icon:require("../../assets/img/wallet/okt.png"),
          txt:"OKExWallet"
        },
        {
          icon:require("../../assets/img/wallet/metamask-fox.svg"),
          txt:"Metamask"
        },
        {
          icon:require("../../assets/img/wallet/tp.png"),
          txt:"TokenPocket"
        },
        {
          icon:require("../../assets/img/wallet/huobi.png"),
          txt:"HuobiWallet"
        },
        {
          icon:require("../../assets/img/wallet/math.png"),
          txt:"MathWallet"
        },
        {
          icon:require("../../assets/img/wallet/info.png"),
          txt:"ONTO Wallet"
        },
        {
          icon:require("../../assets/img/wallet/nabox.png"),
          txt:"Nabox"
        },
        {
          icon:require("../../assets/img/wallet/herpy.png"),
          txt:"HyperPay"
        },
        {
          icon:require("../../assets/img/wallet/bitkeep.png"),
          txt:"BitKeep"
        },
        {
          icon:require("../../assets/img/wallet/aolink.png"),
          txt:"AoLink"
        },
        {
          icon:require("../../assets/img/wallet/coinhub.png"),
          txt:"Coinhub"
        },
        {
          icon:require("../../assets/img/wallet/bitkeep.png"),
          txt:"Bitpie"
        },
      ],
      walletsShort:[
        {
          icon:require("../../assets/img/wallet/okt.png"),
          txt:"OKExWallet"
        },
        {
          icon:require("../../assets/img/wallet/metamask-fox.svg"),
          txt:"Metamask"
        },
        
      ],
      netName:''
    }
  },
  computed: { 
  },
  created () {},
  mounted () {
    NCWeb3.Init(addr => {
	    // console.log(addr)
      this.accountAddr = addr
      let strfirst = addr.substring(0,6)
      let strlast = addr.substring(addr.length - 4)
      this.curAddr = strfirst + '...' + strlast
      this.skipLink = 'https://www.oklink.com/okexchain/address/' + addr
      // console.log(this.skipLink)
      this.getBalnce()
      this.getNetName()
		})
  },
  beforeDestroy() {   
  },
  methods: {
    toMenu(e){
		  // console.log(e)
      if(e == 0){
        window.location.href = 'http://swap-oec.pgo.farm/'
      }
	  },
    async getBalnce(){
      this.balanceOkt = await web3.eth.getBalance(window.defaultAccount)
      this.balanceOkt = Number(window.web3.utils.fromWei(this.balanceOkt.toString())).toFixed(4)
    },
    addrInfor(){
      this.showLine = true
    },
    copyAddr(){
      let clipboard = new Clipboard('.tag-read')
      clipboard.on('success', (e) => {
        Toast.success('Copy succeeded')
          //  
        clipboard.destroy()
      })
      clipboard.on('error', (e) =>{
        // 
        Toast.fail('')
        // 
        clipboard.destroy()
      })
    },
    connectWallet(){
      this.switchWallet = true
    },
    selectWallet(){
      NCWeb3.Init(addr => {
	    // console.log(addr)
        this.accountAddr = addr
        let strfirst = addr.substring(0,6)
        let strlast = addr.substring(addr.length - 4)
        this.curAddr = strfirst + '...' + strlast
        this.skipLink = 'https://www.oklink.com/okexchain/address/' + addr
        // console.log(this.skipLink)
        this.getBalnce()
        this.switchWallet = false
		  })
    },
    async getNetName(){
      let codeId = await web3.eth.net.getId()
      // console.log(codeId)
      // web3.eth.getCoinbase((err, coinbase) => {
 			// 	console.log(coinbase)
 		  // }) ; 
      switch(codeId){
        case 66:
          this.netName = 'OEC';
          break;
        case 65:
          this.netName = 'OECTest';
          break;
        case 128:
          this.netName = 'HECO';
          break;
        case 1:
          this.netName = 'ETH';
          break;
        case 3:
          this.netName = 'Ropsten';
          break;
        case 42:
          this.netName = 'Kovan';
          break;
        case 4:
          this.netName = 'Rinkeby';
          break;
        case 5:
          this.netName = 'Goerli';
          break;
        case 56:
          this.netName = 'BSC';
          break;
        case 97:
          this.netName = 'BSCTest';
          break;
        default:
          this.netName = 'Unknown';
      }
      // if(codeId == 66){
      //   this.netName = 'OEC';
      // }else if(codeId == 65){
      //   this.netName = 'OECTest'
      // }else if(codeId == 128){
      //   this.netName = 'HECO'
      // }else if(codeId == 1){
      //   this.netName = 'ETH'
      // }else if(codeId == 3){
      //   this.netName = 'Ropsten'
      // }else if(codeId == 42){
      //   this.netName = 'Kovan'
      // }else if(codeId == 4){
      //   this.netName = 'Rinkeby'
      // }else if(codeId == 5){
      //   this.netName = 'Goerli'
      // }else if(codeId == 56){
      //   this.netName = 'BSC'
      // }else if(codeId == 97){
      //   this.netName = 'BSCTest'
      // }else{
      //   this.netName = 'Unknown'
      // }
    },
  }
}
</script>
<style lang="less" scoped>
  .heder-nav{
    width: 100%;
    display: flex;
    padding: 1rem 1rem 0 1rem;
    align-items: center;
    justify-content: space-between;
    .right-line{
      display: flex;
      align-items: center;
    }
    .cur-balance{
      font-weight: 600;
      margin: 0 8px 0 0;
      display: flex;
      align-items: center;
      em{
        color: #f3845f;
        padding: 6px 12px;
        margin: 0 10px 0 0;
        background: rgba(243, 132, 95, .1);
      }
    }
    .header-right{
      margin: 0 30px 0 0;
      border: 1px #0049DC solid;
      border-radius: 20px;
      padding: 3px 10px;
      font-weight: 500;
      cursor: pointer;
      .conect-net{
        background: url(../../assets/img/icon-addr.png) no-repeat right;
        background-size: 20px 20px;
        padding: 0 30px 0 0;
        font-weight: 600;
      }
    }
    .header-left{
      display: flex;
      align-items: center;
    }
    .logo{
      img{
        width: 95px;
      }
    }
    .nav{
      display: block; 
    }
    .nav-menu{
      display: flex;
      align-items: center;
      margin: 0 0 0 50px;
      .active{
        color: #0049DC;
      }
      a{
        margin: 0 20px;
        font-size: 16px;
        font-weight: 400;
      }
    }
    .account-infor{
      padding: 5px 20px;
      // border-radius: 20px;
      width: 80%;
      max-width: 380px;
      h3{
        font-size: 16px;
        font-weight: 600;
        margin: 12px 0;
      }
      h5{
        margin: 60px 0 20px 0;
      }
      .account-infor-line{
        border: 1px #eee solid;
        padding: 15px 15px;
      }
      h4{
        color: #0049DC;
        font-size: 12px;
      }
      h2{
        font-size: 20px;
        color: #000;
        background: url(../../assets/img/icon-addr.png) no-repeat left 6px;
        padding: 0 0 0 24px;
        margin: 12px 0;
        background-size: 16px 16px;
      }
      .view-infor{
        font-size: 12px;
        color: #0049DC;
        display: flex;
        align-items: center;
        div{
          cursor: pointer;
          height: 20px;
        }
        div:hover{
          color: #333;
        }
        .link-detail{
          margin: 0 0 0 30px;
          .el-link{
            font-size: 12px;
            color: #0049DC;
          }
        }
        .link-detail:hover{
          text-decoration: underline;
        }
        .copy-addr{
          background: url(../../assets/img/icon-copy.svg) no-repeat left;
          padding: 0 0 0 20px;
          // margin: 12px 0;
          background-size: 13px 18px;
          color: #0049DC;
          margin: 0 0 0 10px;
        }
        .link-detail{
          background: url(../../assets/img/icon-share.svg) no-repeat 0 1px;
          padding: 0 0 0 20px;
          // margin: 12px 0;
          background-size: 13px 18px;
        }
      }
    }
  }
  .wallets{
    // background-color:red;
    border-radius: 25px;

    width: 100%;
    max-width: 375px;
    max-height: 90vh;
  }
  .top>h1{
    font-size: 22px;
    font-weight: bold;
    color: #000;
  }
  .top{
    padding: 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .top>span>i{
    font-size: 26px;
    font-weight: bold;
    color: var(--sec-font);
  }
  .line{
    width: 100%;
    height: 1px;
    background-color: #efefef;
  }
  .lists{
    padding: 15px 25px;
    max-height: 60vh;
    overflow: auto;
  }
  .list{
    height: 56px;
    background-color: #efefef;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 25px;
    margin-top: 15px;
  }
   .list:hover{
     cursor: pointer;
   }
  .list>img{
    width: 32px;
    height: 32px;
    border-radius: 50%; 
  }
  .list>span{
    font-weight: bold;
    color: var(--font-color);
  }
  .arrow{
    margin: 0 25px;
    background-color: #efefef;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 12px;
    border-radius: 20px;
    color: var(--sec-font);
  }
  .arrow:hover{
    cursor: pointer;
  }
  .arrow>span{
    margin-right: 5px;
  }
  .arrow>i{
    font-size: 16px;
    font-weight: bold;
  }
  .bottom{
    // background-color: red;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    font-weight: bold;
    margin-top: 26px;
    color: var(--sec-font);
    margin-bottom: 30px;
  }
  .bottom>i{
    margin-right: 5px;
    font-size: 23px;
  }
  .bottom>a{
    color: var(--sec-font);

  }
  .bottom>a:hover{
    color: var(--font-color);
    // text-decoration: underline;
    // text-decoration-color:var(--font-color);
  }
  .arrowTransform{
    transition: 0.2s;
    transform-origin: center;
    transform: rotateZ(180deg);
  }
  .arrowTransformReturn{
    transition: 0.2s;
    transform-origin: center;
    transform: rotateZ(0deg);
  }
</style>