<template>
  <div class="">
    <Header/>
    <router-view/>
    <FooterBar/>
  </div>
</template>
<script>
import Header from '../components/header/index'
import FooterBar from '../components/footerBar/index'
import NCWeb3 from "../assets/js/web3"
export default {
  name: 'Menu',
  components: {
    Header,
    FooterBar,
  },
  props: {},
  data () {
    return {
      
    }
  },
  computed: { 
  },
  watch: {},
  created () {},
  mounted () {
    NCWeb3.Init(addr => {
      this.changeNet()
      this.switchNet()
		})
  },
  beforeDestroy() {
      
  },
  inject:[ 'reload'],
  methods: {
    
    changeNet(){
      let ethereum = window.ethereum
      if (typeof  ethereum !== 'undefined' || (typeof window.web3 !== 'undefined')) {
        ethereum.on('accountsChanged', (accounts) => {
          this.reload()
        })
        ethereum.on('chainChanged', (chainId) => {    
          this.reload()
        })
        ethereum.on('networkChanged', (networkVersion) => {
          this.reload()
        })
      }else {
          // alert('You have to install MetaMask !')
      }
    },
    switchNet(){
      // console.log(ethereum.isMetaMask)
  		// let id = ethereum.chainId;
  		// console.log(id)
      let OKTMainnet = {
        chainId:'0x42',rpcUrls:['https://exchainrpc.okex.org'],chainName:'OKExChain',nativeCurrency:{
            name: 'OKT',
              symbol: 'OKT', // 2-6 characters long
              decimals: 18,
        },blockExplorerUrls:['https://www.oklink.com/okexchain/']
      };
      this.switchChain(OKTMainnet)
    },
    async switchChain(data){
  	  try {
  			let {chainId} = data;
  	    await ethereum.request({
  	      method: 'wallet_switchEthereumChain',
  	      params: [{ chainId }],
  	    });
		
  	  } catch (switchError) {
  	    // This error code indicates that the chain has not been added to MetaMask.
  			console.log(switchError)
  	    if (switchError.code === 4902) {
  	      
  			  this.addChain(data);
  	    }
  	    // handle other "switch" errors
  	  }
    },
    async addChain(data){
      try {
        await ethereum.request({
          method: 'wallet_addEthereumChain',
          params: [data],
        });
      } catch (addError) {
        console.log(addError)
        // handle "add" error
      }
    },
  }
}
</script>
